  "HEALTH_DATA_NOTIFICATION_MESSAGE": "{APP_NAME} മെച്ചപ്പെടുത്തുന്നതിന്, നിങ്ങൾ {APP_NAME} എങ്ങനെ ഉപയോഗിക്കുന്നു എന്നതിനെക്കുറിച്ചുള്ള പരിമിതമായ, <strong>ഓമനപ്പേരുള്ള</strong> സ്ഥിതിവിവരക്കണക്കുകളും പിശക് റിപ്പോർട്ടുകളും ഞങ്ങൾ ആനുകാലികമായി phcode.dev-ലേക്ക് അയയ്ക്കുന്നു. നിങ്ങളുടെ സ്വകാര്യത കാത്തുസൂക്ഷിക്കുമ്പോൾ സവിശേഷതകൾക്ക് മുൻഗണന നൽകാനും ബഗുകൾ കണ്ടെത്താനും ഉപയോഗക്ഷമത പ്രശ്നങ്ങൾ കണ്ടെത്താനും ഈ വിവരങ്ങൾ സഹായിക്കുന്നു.<br><br> <strong>സഹായം &gt; ആരോഗ്യ റിപ്പോർട്ട്</strong> തിരഞ്ഞെടുത്ത് നിങ്ങൾക്ക് നിങ്ങളുടെ ഡാറ്റ കാണാനോ ഡാറ്റ പങ്കിടരുതെന്ന് തിരഞ്ഞെടുക്കാനോ കഴിയും.<br><br>",
  "HEALTH_DATA_PREVIEW": "{APP_NAME} ആരോഗ്യ റിപ്പോർട്ട്",
  "HEALTH_DATA_PREVIEW_INTRO": "<p>{APP_NAME} മെച്ചപ്പെടുത്തുന്നതിന്, നിങ്ങൾ {APP_NAME} എങ്ങനെ ഉപയോഗിക്കുന്നു എന്നതിനെക്കുറിച്ചുള്ള പരിമിതമായ, <strong>ഓമനപ്പേരുള്ള</strong> സ്ഥിതിവിവരക്കണക്കുകളും പിശക് റിപ്പോർട്ടുകളും ഞങ്ങൾ ആനുകാലികമായി phcode.dev-ലേക്ക് അയയ്ക്കുന്നു. ഫീച്ചറുകൾക്ക് മുൻഗണന നൽകാനും ബഗുകൾ കണ്ടെത്താനും ഉപയോഗക്ഷമത പ്രശ്നങ്ങൾ കണ്ടെത്താനും ഈ വിവരങ്ങൾ സഹായിക്കുന്നു.</p><p> <em>നിങ്ങളുടെ</em> അടുത്ത ആരോഗ്യ റിപ്പോർട്ട് പ്രവർത്തനക്ഷമമാക്കിയാൽ അതിൽ അയയ്‌ക്കുന്ന ഡാറ്റയുടെ പ്രിവ്യൂ ചുവടെയുണ്ട്. (&#39;കാറ്റ് ക്രിട്ടിക്കൽ എറർ&#39; എന്ന് അടയാളപ്പെടുത്തിയ പിശക് ലോഗുകൾക്കായി ഡെവലപ്പർ കൺസോളും കാണുക.)</p>",
