  "PHP_UNSUPPORTED_VERSION": "കോഡ് സൂചനകൾ, പാരാമീറ്റർ സൂചനകൾ, ജമ്പ് ടു ഡെഫിനിഷൻ എന്നിവയും അതിലേറെയും പോലെയുള്ള PHP-യുമായി ബന്ധപ്പെട്ട ടൂളിംഗ് പ്രവർത്തനക്ഷമമാക്കുന്നതിന് PHP7 റൺടൈം ഇൻസ്റ്റാൾ ചെയ്യുക. പതിപ്പ് കണ്ടെത്തി: {0}",
  "PHP_EXECUTABLE_NOT_FOUND": "PHP റൺടൈം കണ്ടെത്തിയില്ല. PHP7 റൺടൈം ഇൻസ്റ്റാൾ ചെയ്ത് PHP മുൻഗണനകളിൽ ഉചിതമായി “എക്‌സിക്യൂട്ടബിൾപാത്ത്” അപ്‌ഡേറ്റ് ചെയ്യുക. ഇത് കോഡ് സൂചനകൾ, പാരാമീറ്റർ സൂചനകൾ, ജമ്പ് ടു ഡെഫനിഷൻ എന്നിവയും അതിലേറെയും പോലുള്ള പിഎച്ച്പിയുമായി ബന്ധപ്പെട്ട ടൂളിംഗ് പ്രവർത്തനക്ഷമമാക്കുന്നു.",
  "PHP_PROCESS_SPAWN_ERROR": "PHP പ്രോസസ്സ് ആരംഭിക്കുമ്പോൾ പിശക് കോഡ് {0} നേരിട്ടു.",
  "PHP_SERVER_ERROR_TITLE": "പിശക്",
  "PHP_SERVER_MEMORY_LIMIT_INVALID": "നിങ്ങൾ നൽകിയ മെമ്മറി പരിധി അസാധുവാണ്. ശരിയായ മൂല്യം സജ്ജമാക്കാൻ PHP മുൻഗണനകൾ അവലോകനം ചെയ്യുക.",
  "DESCRIPTION_PHP_TOOLING_CONFIGURATION": "PHP ടൂളിംഗ് ഡിഫോൾട്ട് കോൺഫിഗറേഷൻ ക്രമീകരണങ്ങൾ",
