  "REMOTE_DEBUGGING_ENABLED": "ലോക്കൽ ഹോസ്റ്റിൽ റിമോട്ട് ഡീബഗ്ഗിംഗ് പ്രവർത്തനക്ഷമമാക്കി:",
  "REMOTE_DEBUGGING_PORT_INVALID": "{0} പോർട്ടിൽ റിമോട്ട് ഡീബഗ്ഗിംഗ് പ്രവർത്തനക്ഷമമാക്കാൻ കഴിയില്ല. പോർട്ട് നമ്പറുകൾ {1} നും {2} നും ഇടയിലായിരിക്കണം.",
  "DESCRIPTION_EXTERNAL_APPLICATION_ASSOCIATE": "ബാഹ്യ ആപ്ലിക്കേഷനുകളിലേക്കുള്ള ഫയൽ വിപുലീകരണത്തിനുള്ള മാപ്പിംഗുകൾ. വാക്യഘടന: \"<file_type>\": \"<default|applicationName|ApplicationPath>\", ഫയൽ തരത്തിനായി സിസ്റ്റം ഡിഫോൾട്ട് ആപ്ലിക്കേഷനിൽ ഫയലുകൾ തുറക്കാൻ \"default\" ഉപയോഗിക്കുക.",
  "ASSOCIATE_GRAPHICS_FILE_TO_DEFAULT_APP_TITLE": "ബാഹ്യ എഡിറ്ററുകളിൽ ഗ്രാഫിക് ഫയലുകൾ തുറക്കുക.",
  "ASSOCIATE_GRAPHICS_FILE_TO_DEFAULT_APP_MSG": "നിങ്ങളുടെ നിലവിലെ ഫോൾഡറിൽ {APP_NAME} പിന്തുണയ്ക്കാത്ത ഗ്രാഫിക് ഫയൽ തരങ്ങളുണ്ട്.<br/> നിങ്ങൾക്ക് ഇപ്പോൾ ബാഹ്യ എഡിറ്റർമാരുമായി നിർദ്ദിഷ്ട ഫയൽ തരങ്ങളെ ബന്ധപ്പെടുത്താം. ഒരിക്കൽ ബന്ധപ്പെടുത്തിക്കഴിഞ്ഞാൽ, ഫയൽ ട്രീയിലെ ഫയലുകളിൽ ഡബിൾ ക്ലിക്ക് ചെയ്തുകൊണ്ട് .xd, .psd, .jpg, .png, .ai, .svg തുടങ്ങിയ ഗ്രാഫിക് ഫയലുകൾ അവയുടെ ഡിഫോൾട്ട് ആപ്ലിക്കേഷനുകളിൽ തുറക്കാനാകും.<br/><br/> ഗ്രാഫിക് ഫയൽ തരങ്ങളെ അവയുടെ ഡിഫോൾട്ട് ആപ്ലിക്കേഷനുകളുമായി ബന്ധപ്പെടുത്തുന്നതിന് ദയവായി &#39;ശരി&#39; ബട്ടണിൽ ക്ലിക്ക് ചെയ്യുക.",
  "ASSOCIATE_GRAPHICS_FILE_TO_DEFAULT_APP_CNF_MSG": "ഇനിപ്പറയുന്ന ഫയൽ തരങ്ങൾ ഡിഫോൾട്ട് ആപ്ലിക്കേഷനുകളുമായി വിജയകരമായി ബന്ധപ്പെടുത്തിയിരിക്കുന്നു.<br/> {0} &quot;ഡീബഗ്-&gt; ഓപ്പൺ പ്രിഫറൻസസ് ഫയൽ&quot; മെനുവിലേക്ക് പോയി ബ്രാക്കറ്റുകൾ.json-ൽ പുതിയ ഫയൽ തരം അസോസിയേഷനുകൾ ഇല്ലാതാക്കണോ/ചേർക്കണോ എന്നതിലുള്ള നിങ്ങളുടെ മുൻഗണന മാറ്റാനുള്ള ഓപ്ഷൻ നിങ്ങൾക്കുണ്ട്.",
  "UNSUPPORTED_BROWSER_TITLE": "ബ്രൗസർ പിന്തുണയ്ക്കുന്നില്ല",
  "UNSUPPORTED_BROWSER_MESSAGE": "ദയവായി ഒരു ആധുനിക ബ്രൗസർ ഉപയോഗിക്കുക (കഴിഞ്ഞ 3 വർഷത്തിനുള്ളിൽ റിലീസ് ചെയ്തത്).",
  "UNSUPPORTED_BROWSER_MOBILE_TITLE": "മൊബൈൽ ബ്രൗസറുകൾ പിന്തുണയ്ക്കുന്നില്ല",
  "UNSUPPORTED_BROWSER_MOBILE": "മൊബൈൽ, ടാബ്‌ലെറ്റ് ബ്രൗസറുകൾ നിലവിൽ പൂർണ്ണമായി പിന്തുണയ്ക്കുന്നില്ല. നിങ്ങൾക്ക് ഈ ഡയലോഗ് അടയ്‌ക്കാം, എന്നാൽ ഫീനിക്‌സിലെ ചില സവിശേഷതകൾ ഈ ബ്രൗസറിൽ പ്രവർത്തിക്കില്ല.<br/><br/> iOS, Android എന്നിവയ്‌ക്കായി നേറ്റീവ് ആപ്പുകൾ വികസിപ്പിക്കാൻ ഞങ്ങൾ പ്രവർത്തിക്കുന്നു. <a href=\"https://github.com/phcode-dev/phoenix/discussions/1046\" target=\"_blank\">ഇവിടെ ചർച്ചയിൽ ചേരൂ!</a>",
  "UNSUPPORTED_BROWSER_OPEN_FOLDER_TITLE": "പ്രാദേശിക ഫോൾഡർ ആക്‌സസ്സ് ബ്രൗസർ പിന്തുണയ്ക്കുന്നില്ല",
  "UNSUPPORTED_BROWSER_OPEN_FOLDER": "ക്ഷമിക്കണം, നിങ്ങളുടെ നിലവിലെ ബ്രൗസർ ലോക്കൽ ഫോൾഡറുകൾ തുറക്കുന്നതിനെ പിന്തുണയ്ക്കുന്നില്ലെന്ന് തോന്നുന്നു. ഈ സവിശേഷതയ്ക്കായി, Chrome, Edge അല്ലെങ്കിൽ Opera ഉപയോഗിക്കാൻ ഞങ്ങൾ ശുപാർശ ചെയ്യുന്നു.<br/><br/> പകരമായി, പൂർണ്ണ പിന്തുണ നൽകുന്നതിനായി Windows, Mac, Linux, iOS, Android എന്നിവയ്‌ക്കായി നേറ്റീവ് ആപ്പുകൾ വികസിപ്പിക്കുന്നതിൽ ഞങ്ങൾ പ്രവർത്തിക്കുന്നു. <a href=\"https://github.com/phcode-dev/phoenix/discussions/1047\" target=\"_blank\">അപ്‌ഡേറ്റുകൾക്കായി കാത്തിരിക്കുക!</a>",
  "ATTENTION_SAFARI_USERS": "സഫാരി ഉപയോക്താക്കളുടെ ശ്രദ്ധയ്ക്ക്",
  "ATTENTION_SAFARI_USERS_MESSAGE": "<span>7 ദിവസത്തിനുള്ളിൽ ഒരു സൈറ്റ് വീണ്ടും സന്ദർശിച്ചില്ലെങ്കിൽ Safari സ്വയമേവ വെബ്‌സൈറ്റ് ഡാറ്റ ഇല്ലാതാക്കുന്നു. നിങ്ങളുടെ പ്രോജക്‌റ്റുകൾ സംരക്ഷിക്കാൻ {APP_NAME} ബ്രൗസർ സംഭരണത്തെ ആശ്രയിക്കുന്നു, ഈ നയം കാരണം അത് നീക്കം ചെയ്‌തേക്കാം.<br/><br/> <strong>ഡാറ്റ നഷ്‌ടപ്പെടുന്നത് തടയാൻ</strong> , ടാബ് അടയ്‌ക്കുന്നതിന് മുമ്പ് നിങ്ങളുടെ പ്രോജക്‌റ്റുകൾ <strong>ഡൗൺലോഡ് ചെയ്യുക</strong> അല്ലെങ്കിൽ ഞങ്ങളുടെ <strong>വരാനിരിക്കുന്ന വെബ്, നേറ്റീവ് Mac/iOS ആപ്പുകൾക്കായി</strong> കാത്തിരിക്കുക.</span>",
  "CANNOT_PUBLISH_LARGE_PROJECT": "വലിയ പ്രോജക്റ്റ് പ്രസിദ്ധീകരിക്കാൻ കഴിയില്ല",
  "CANNOT_PUBLISH_LARGE_PROJECT_MESSAGE": "ഫീനിക്സ് ഇപ്പോഴും പരീക്ഷണാത്മക ആൽഫയിലാണ്. 500-ലധികം ഫയലുകളുള്ള പ്രോജക്‌റ്റുകളുടെ സമന്വയം ഞങ്ങൾ ഇതുവരെ പ്രവർത്തനക്ഷമമാക്കിയിട്ടില്ല.",
